<template>
  <div class="home">
    <div class="nav_box">
      <router-link class="nav_item" :to="list.url" v-for="(list,index) in lists" :key="index">
        <span class="count">{{index+1}}</span>
        <span class="more"><img src="../assets/more.png"></span>
        <span class="nav_name">{{list.title}}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      lists:[
        {
          url:'/first',
          pic:'',
          title:'想了解宝贝的价值多少？'
        },
        {
          url:'/second',
          pic:'',
          title:'急需换钱？'
        },
        {
          url:'/third',
          pic:'',
          title:'竞拍绝当宝贝？'
        },
        {
          url:'/forth',
          pic:'',
          title:'买心仪宝贝啦。'
        }
      ],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
      this.$nextTick(function(){
        window.document.title='帮助列表'
      })
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .nav_box{
    margin-top: 20px;
    background: #fff;
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
  }
  .nav_item{
    display: block;
    width: 100%;
    height: 78px;
    line-height: 78px;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #e4e4e4;
    overflow: hidden;
    text-decoration: none;
  }
  .nav_item:last-child{
    border-bottom: none;
  }
  .nav_name{
    display: block;
    height: 78px;
    line-height: 78px;
    font-size: 16px;
    color: #333;
    margin:0 34px 0 38px;
  }
  .count{
    float: left;
    margin-top: 20px;
    width:28px;
    height: 36px;
    text-align: center;
    font-size: 18px;
    padding-top: 6px;
    color: #d11b1b;
    background: url('../assets/count.png') no-repeat;
    background-size: 100%;
    line-height: 1;
  }
  .more img{
    float: right;
    width: 12px;
    height: 22px;
    margin-top: 28px;
  }
</style>



