<template>
  <div>
      <common :box="box"></common>
  </div>
</template>
<script>
import Common from './common'
export default {
  components:{
      Common
  },
  data(){
      return{
          box:[
              {
                  name:'请到认证商城页面。',
                  pic:'./static/help_04/01.jpg'
              },
               {
                  name:'挑选你喜欢的物品，赶紧下手哦。',
                  pic:'./static/help_04/02.jpg'
              },
               {
                  name:'请付款购买，早买心爱的宝贝早到手噢。',
                  pic:'./static/help_04/03.jpg'
              }
          ]
      }
  }
}
</script>




