<template>
  <div>
      <common :box="box"></common>
  </div>
</template>
<script>
import Common from './common'
export default {
  components:{
      Common
  },
  data(){
      return{
          box:[
              {
                  name:'来到估价页面',
                  pic:'./static/help_02/01.jpg'
              },
               {
                  name:'赶紧去典当吧,填写所需资料。',
                  pic:'./static/help_02/02.jpg'
              },
               {
                  name:'请实名认证',
                  pic:'./static/help_02/03.jpg'
              },
               {
                  name:'确认发布，完成后，静静等待钱款到账。',
                  pic:'./static/help_02/04.jpg'
              }
          ]
      }
  }
}
</script>




