<template>
  <div>
      <common :box="box"></common>
  </div>
</template>
<script>
import Common from './common'
export default {
  components:{
      Common
  },
  data(){
      return{
          box:[
              {
                  name:'来到宝贝估价页面',
                  pic:'./static/help_01/01.jpg'
              },
               {
                  name:'选择钻石，填写资料。',
                  pic:'./static/help_01/02.jpg'
              },
               {
                  name:'鉴定结果出来啦，您的宝贝真不错！',
                  pic:'./static/help_01/03.jpg'
              }
          ]
      }
  }
}
</script>




