<template>
  <div>
      <common :box="box"></common>
  </div>
</template>
<script>
import Common from './common'
export default {
  components:{
      Common
  },
  data(){
      return{
          box:[
              {
                  name:'请到绝当商城页面，挑选自己喜欢的商品。',
                  pic:'./static/help_03/01.jpg'
              },
               {
                  name:'3万以上价高者得，3万以下可以直接购买哦。',
                  pic:'./static/help_03/02.jpg'
              },
               {
                  name:'请付款购买，早买心爱的宝贝早到手噢。',
                  pic:'./static/help_03/03.jpg'
              }
          ]
      }
  }
}
</script>




