<template>
    <div>
        <ul>
            <li v-for="(item,index) in box" :key="item.name">
                <p class="common_title">{{index+1}}.{{item.name}}</p>
                <img :src="item.pic">
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  name:'common',
  props:['box']
}
</script>
<style>
.common_title{
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 15px;
    color: #333;
    padding: 0 20px;
    box-sizing: border-box;
}
</style>





