import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import FirstHelp from '@/components/first_help'
import SecondHelp from '@/components/second_help'
import ThirdHelp from '@/components/third_help'
import ForthHelp from '@/components/forth_help'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/first',
      name: 'FirstHelp',
      component: FirstHelp
    },
    {
      path: '/second',
      name: 'SecondHelp',
      component: SecondHelp
    },{
      path: '/third',
      name: 'ThirdHelp',
      component: ThirdHelp
    }
    ,{
      path: '/forth',
      name: 'ForthHelp',
      component: ForthHelp
    }
  ]
})



